<template>
  <v-container id="regular-tables" fluid tag="section">
    <base-material-card
      icon="mdi-clipboard-text"
      title=" "
      class="px-md-12 py-3"
    >
      <template slot="after-heading">
         
      </template>

      <v-form ref="form" lazy-validation>
        <h2 class="text-right">   </h2>
        <v-row>
          <v-col cols="12" md="6">
            <v-autocomplete
              :rules="[v => !!v || '  ']"
              solo
              :items="contentData.schools"
              label="    "
              :menu-props="{ down: true, offsetY: true }"
              v-model="shortageModel.school"
            ></v-autocomplete>
          </v-col>
          <v-col cols="12" md="6">
            <v-select
              solo
              :items="contentData.grades"
              :rules="[v => !!v || ' ']"
              :menu-props="{ down: true, offsetY: true }"
              v-model="shortageModel.grade"
              return-object
              item-text="name"
              label=""
            ></v-select>
          </v-col>
          <v-col cols="12" md="6">
            <v-select
              solo
              :items="gradeTypes"
              :rules="[v => !!v || '  ']"
              :menu-props="{ down: true, offsetY: true }"
              v-model="shortageModel.gradeType"
              label=""
            ></v-select>
          </v-col>
          <v-col cols="12" md="6">
            <v-select
              solo
              :items="contentData.materials"
              :menu-props="{ down: true, offsetY: true }"
              :rules="[v => !!v || ' ']"
              return-object
              v-model="shortageModel.material"
              item-text="name"
              label=" "
            ></v-select>
          </v-col>
          <v-col cols="12" md="6">
            <v-text-field
              label=" "
              :rules="[v => !!v || '  ']"
              v-model="shortageModel.hours"
              solo
              class="mb-3"
              type="number"
            ></v-text-field>
          </v-col>
        </v-row>
        <div class="text-right">
          <v-btn @click="submit" :loading="submitting" depressed></v-btn>
        </div>
      </v-form>
    </base-material-card>
    <v-dialog v-model="errorDialog" width="500">
      <v-card>
        <v-card-title class="headline red accent-2 white--text">
           
        </v-card-title>

        <v-card-text class="pt-3">
                  
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-btn color="red accent-2" text @click="errorDialog = false">
            
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>

    <v-dialog v-model="successDialog" width="500">
      <v-card>
        <v-card-title class="headline pb-2 text-right green white--text">
           
        </v-card-title>

        <v-card-text class=" text-right">
             
        </v-card-text>

        <v-divider></v-divider>

        <v-card-actions>
          <v-spacer> </v-spacer>
          <v-btn color="primary" text @click="closeSuccessDialog">
            
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
    <div class="py-3" />
  </v-container>
</template>
<script>
import http from "@/repo/teachAndLearnHttp";
export default {
  data() {
    return {
      shortageModel: {},
      contentData: {
        schools: [],
        grades: [],
        materials: []
      },
      submitting: false,
      errorDialog: false,
      successDialog: false,
      gradeTypes: [
        {
          text: "",
          value: "s"
        },
        {
          text: "",
          value: "l"
        },
        {
          text: "",
          value: "g"
        },
        {
          text: "",
          value: "c"
        }
      ]
    };
  },
  async mounted() {
    await this.fetchContentData();
  },
  methods: {
    async fetchContentData() {
      let { data } = await http().get("shortage/create");
      this.contentData = data;
    },
    closeSuccessDialog() {
      this.successDialog = false;
      this.$router.push("/shortages");
    },
    submit() {
      if (!this.$refs.form.validate()) return;
      let fd = new FormData();
      fd.append("school_name", this.shortageModel.school);
      fd.append("grade_id", this.shortageModel.grade.id);
      fd.append("grade_name", this.shortageModel.grade.name);
      fd.append("grade_type", this.shortageModel.gradeType);
      fd.append("material_id", this.shortageModel.material.id);
      fd.append("material_name", this.shortageModel.material.name);
      fd.append("houres", this.shortageModel.hours);

      this.submitting = true;

      http()
        .post("shortage", fd)
        .then(res => {
          this.submitting = false;
          this.successDialog = true;
        })
        .catch(res => {
          this.submitting = false;
          this.errorDialog = true;
        });
    }
  }
};
</script>
